
import { defineComponent, onMounted } from 'vue'
import { Chart } from '@antv/g2'

export default defineComponent({
  setup() {
    onMounted(() => {
      const data = [
        { type: '', value: 340, cat: '' },
        { type: '', value: 20760, cat: '' },
        { type: '', value: 28750, cat: '' },
        { type: '', value: 4090, cat: '' },
        { type: '', value: 9880, cat: '' },
        { type: '', value: 40988, cat: '' },
        { type: '', value: 14870, cat: '' },
        { type: '', value: 37098, cat: '' },
        { type: '', value: 49099, cat: '' }
      ]
      const chart: any = new Chart({
        container: 'line-chart',
        autoFit: true,
        // height: 500,
        padding: [20, 0, 50, 100]
      } as any)
      chart.data(data)
      chart.scale({
        value: {
          max: 55000,
          min: 0,
          alias: ''
        }
      })
      chart.axis('type', {
        tickLine: null,
        line: null
      })
      chart.axis('value', {
        label: null,
        title: {
          offset: 30,
          style: {
            fontWeight: 300
          }
        },
        grid: null
      })
      chart.legend(false)
      chart.coordinate('rect').transpose()
      chart
        .interval()
        .position('type*value')
        .color('cat', ['#face1d', '#37c461', '#2194ff'])
        .size(26)
        .label('value', {
          style: {
            fill: '#8d8d8d'
          },
          offset: 10,
          content: (originData: any) => {
            return (originData.value + '').replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
          }
        })

      chart.annotation().text({
        top: true,
        position: ['', 'min'],
        content: '',
        style: {
          fill: '#c0c0c0',
          fontSize: 12,
          fontWeight: '300',
          textAlign: 'center'
        },
        offsetX: -70,
        rotate: Math.PI * -0.5
      })
      chart.annotation().text({
        top: true,
        position: ['', 'min'],
        content: '',
        style: {
          fill: '#c0c0c0',
          fontSize: 12,
          fontWeight: '300',
          textAlign: 'center'
        },
        offsetX: -70,
        rotate: Math.PI * -0.5
      })
      chart.annotation().text({
        top: true,
        position: ['', 'min'],
        content: '',
        style: {
          fill: '#c0c0c0',
          fontSize: 12,
          fontWeight: '300',
          textAlign: 'center'
        },
        offsetX: -70,
        rotate: Math.PI * -0.5
      })
      chart.annotation().line({
        start: ['-20%', '33.2%'],
        end: ['100%', '33.2%'],
        style: {
          stroke: '#c0c0c0',
          lineDash: [2, 2]
        }
      })
      chart.annotation().line({
        start: ['-20%', '66.8%'],
        end: ['100%', '66.8%'],
        style: {
          stroke: '#c0c0c0',
          lineDash: [2, 2]
        }
      })
      chart.interaction('element-active')

      chart.render()
    })
    return {}
  }
})
